import React, { useState } from 'react';


function Skills() {
    let [skills, setSkills] = useState([
        {name: 'html / html5', percentage: 95, id: 1},
        {name: 'css / css3', percentage: 95, id: 2},
        {name: 'sass', percentage: 85, id: 3},
        {name: 'javascript / es6', percentage: 85, id: 4},
        {name: 'jquery', percentage: 91, id: 5},
        {name: 'bootstrap 3 / 4', percentage: 90, id: 6},
        {name: 'git / github', percentage: 99, id: 7},
        {name: 'react js', percentage: 89, id: 8},
        {name: 'redux', percentage: 80, id: 9},
        {name: 'angular', percentage: 77, id: 10},
        {name: 'vue', percentage: 75, id: 11},
        {name: 'php', percentage: 83, id: 12},
        {name: 'mysql', percentage: 85, id: 13},
        {name: 'wordpress', percentage: 71, id: 14},
        {name: 'laravel8', percentage: 60, id: 15},
        {name: 'shopify / liquid', percentage: 87, id: 16}
    ]);
    return ( 
        <div className='skills'>
            <h3 className='title'>my skills</h3>
            <div className='container'>

            {
                                
                skills.map(function (x) {
                    return (
                        <div className='skill' key={x.id}>
                            <h3 className='skill-name'>{x.name}</h3>
                            <div className='bar'>
                                <span className='percentage'>{x.percentage}%</span>
                                <span className='expertise-bar' style={{'width': x.percentage + '%'}}></span>
                            </div>
                        </div>
                    )
                })
            }

            </div>
        </div>
    );
}

export default Skills;