import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom/cjs/react-router-dom.min';

function Navbar() {
    let [links, setLinks] = useState([
        {
            link: "/",
            text: "home",
            id: 1
        },
        {
            link: "/about",
            text: "about us",
            id: 2
        },
        {
            link: "/services",
            text: "services",
            id: 3
        },
        {
            link: "/contact",
            text: "contact",
            id: 4
        },
    ]);

    let location = useLocation();
    function toggleMenu(e) {
        var menuElement = document.getElementById("main-menu");
        var menuSwitck = document.getElementById("menu-switch");
        menuElement.classList.toggle("open");
        menuSwitck.classList.toggle("open");        
    }
    return ( 
        <div className="navbar">
            <div className="container">
                <div className="logo">sbedo</div>
                <div className="menu-container">
                    <div className="menu-switch" onClick={toggleMenu} id="menu-switch">
                        <span className="bar"></span>
                        <span className="bar"></span>
                        <span className="bar"></span>
                        <span className="overlay"></span>
                    </div>
                    <div className="menu" id="main-menu">
                        <ul>
                            {
                                
                                links.map(function (x) {
                                    return (
                                        <li key={x.id} className={location.pathname == x.link ? 'active' : ''} ><Link to={x.link} onClick={toggleMenu}>{x.text}</Link></li>
                                    )
                                })
                            }
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Navbar;