import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCode, faDraftingCompass, faShoppingCart, faAddressCard, faDatabase} from '@fortawesome/free-solid-svg-icons';

function Services() {
    let [services, setServices] = useState([
        {id: 1, icon: faDraftingCompass, title: "design front end", discription: 'design and maintain the UI and UX of the site with efficiency, pixel perfect and responsiveness in mind'},
        {id: 2, icon: faCode, title: "web development", discription: 'develop the front and back end of the site with highly professional, clean and easy to read code.'},
        {id: 3, icon: faShoppingCart, title: "e-commerce managment", discription: 'develop professional e-commerce with wide choices platform like wordpress, shopify, magento, wix, laravel...'},
        {id: 4, icon: faAddressCard, title: "personal portfolio", discription: 'design professional and eye catching protfolio'},
        {id: 5, icon: faDatabase, title: "architecte databases", discription: 'build and maintain database systems'},
    ]);
    return ( 
        <div className="services">
            <h2 className='heading'>
                <span>my services</span>
            </h2>
            
            {/* services */}
            <div className='services'>
                <div className='container'>
                {
                                
                    services.map(function (x) {
                        return (
                            <div className='service' key={x.id}>
                                <div className='ser-container'>
                                    <div className='icon'>
                                        <FontAwesomeIcon icon={x.icon}/>
                                    </div>
                                    <h3 className='title'>{x.title}</h3>
                                    <p>{x.discription}</p>
                                </div>
                            </div>
                        )
                    })
                }
                </div>
            </div>
        </div>
     );
}

export default Services;